
import { UsuarioService } from './../../../service/sigobra/usuario.service';
import { Base } from './../../../model/base';
import { Usuario } from './../../../model/objetc/usuario';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Pessoa } from '../../../model/objetc/pessoa';
import { Empresa } from '../../../model/objetc/empresa';
import { IonicSelectableComponent } from 'ionic-selectable';
import { EmpresaService } from '../../../service/sigobra/empresa.service';


@Component({
  selector: 'app-usuario-inserir',
  templateUrl: './usuario-inserir.page.html',
  styleUrls: ['./usuario-inserir.page.scss'],
})
export class UsuarioInserirPage implements OnInit {

  empresas: Array<Empresa>;
  usuario: Usuario = new Usuario();
  pessoa: Pessoa = new Pessoa();
  constructor(private router: Router, private base: Base,
    private es: EmpresaService,
    private us: UsuarioService) { }

  selecionarusuario(evente: any) {

  }
  ngOnInit() {
    this.carregarEnpresas(" ");
    if (this.us.usuario !== undefined && this.us.usuario !== null) {
      this.usuario = this.us.usuario;
      this.pessoa = this.usuario.pessoa;
    }
  }

  remover(usuario: any) {

  }

  salvar() {
    this.base.present();
    this.pessoa.ativo = true;
    this.usuario.ativo = true;
    this.usuario.pessoa = this.pessoa;
    this.us.salvar(this.usuario).subscribe(
      data => {
        this.us.usuario = null;
        this.base.dismiss();
        this.router.navigate(['/usuarios']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falhar ao salvar usuario ' + this.base.tratarErro(error));
      }
    );
  }

  keyDownFunction(event: any) {

  }

  selecionarProfissionais(event: any) {

  }

  carregarEnpresas(value: string) {
    return this.es.pesquisar(value).subscribe(
      data => {
        this.empresas = data as Array<Empresa>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  empresa ' + this.base.tratarErro(error));
      }
    );
  }

  selecionarEmpresa(event: {
    component: IonicSelectableComponent,
    empresa: Empresa
  }) {
    this.pessoa.empresa = event.empresa;
  }
}
